import React, { useEffect } from "react";
import F2 from "@antv/f2";
import { IProps, ITextCfg, Defs } from "./constance";
require("./style.scss");

const ChartLine = (props: IProps) => {
  const { mark, data, active } = props;
  let chart: any;
  const initChart = () => {
    chart = new F2.Chart({
      id: `canvas-${mark}`,
      pixelRatio: window.devicePixelRatio
    });
    const tabs = mark.substring(mark.indexOf("-") + 1),
      maskPro = "";
    const MASKPRO = tabs === "2" ? "YYYY-MM" : "YYYY-MM-DD";
    chart.source(data, {
      value: {
        tickCount: 4,
        min: 0
      },
      date: {
        type: "timeCat",
        range: [0, 1],
        tickCount: 3,
        mask: MASKPRO
      }
    });
    chart.tooltip({
      custom: true,
      showXTip: true,
      showYTip: true,
      snap: true,
      crosshairsType: "xy",
      crosshairsStyle: {
        lineDash: [2]
      }
    });
    chart.axis("date", {
      label: function label(text, index, total) {
        const textCfg: ITextCfg = {};
        if (index === 0) {
          textCfg.textAlign = "left";
        } else if (index === total - 1) {
          textCfg.textAlign = "right";
        }
        return textCfg;
      }
    });
    chart.line().position("date*value");
    chart.render();
  };

  useEffect(() => {
    if (data && data.length && active) {
      initChart();
    }
    return () => {
      chart && chart.clear();
      chart = null;
    };
  }, [props.data, props.active]);

  return (
    <div className="chart-line-wrap">
      <canvas className="chart-line" id={`canvas-${mark}`}></canvas>
    </div>
  );
};

export default ChartLine;
