import React from "react";
// import { RouteComponentProps } from 'react-router-dom';
import { observer, inject } from "mobx-react";
import { Tabs, Icon, Picker } from "antd-mobile";
import SubPage from "@/components/SubPage";
import TabItem from "./TabItem";
import { EnumDimension, Istore } from "./constance";
import { Item } from "@/components/ProtectionList";
import EasyNarBar from "@/components/EasyNarBar";
const tabs = [{ title: "日" }, { title: "月" }, { title: "年" }];

// interface IProps extends RouteComponentProps {
//     [key: string]: any;
// }
@inject("alarmStatisticsStore")
@observer
class AlarmStatistics extends React.Component<IProps> {
  componentDidMount() {
    const {
      alarmStatisticsStore: {
        fetchAllAgency,
        mediator,
        allFetch,
        fetchTopTenData,
        initData,
        getAgencyseletedObj,
        fetchPanelData,
        fetchAmountTrendEvent
      }
    } = this.props;
    mediator();
  }
  componentWillUnmount() {
    const {
      alarmStatisticsStore: { initData }
    } = this.props;
    initData();
  }
  render() {
    const {
      store,
      history,
      alarmStatisticsStore: { getAgencyseletedObj, mediator }
    } = this.props;
    const {
      view,
      curPage,
      agency,
      agencyName,
      overviewData,
      alarmTypeData,
      numberChartData,
      timeChartData,
      onSwitchView,
      onPageChange,
      agencyId
    } = store;

    return (
      <>
        <EasyNarBar
          mainContent={
            <Picker
              data={agency}
              cols={1}
              onChange={(choice: any) => {
                getAgencyseletedObj(choice).mediator();
              }}
            >
              <div className="alarm-statistics-title">
                <span>{agencyName}</span>
                <Icon type="down" size="xs" />
              </div>
            </Picker>
          }
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            history.push("/dashboard");
          }}
        ></EasyNarBar>

        <Tabs tabs={tabs} page={curPage} onChange={onPageChange} animated={false} useOnPan={false} swipeable={false}>
          <TabItem
            key={0}
            store={store}
            tab={EnumDimension["day"]}
            mode={"date"}
            format={"YYYY-MM-DD"}
            view={view}
            active={curPage === EnumDimension["day"]}
            overviewData={overviewData}
            alarmTypeData={alarmTypeData}
            numberChartData={numberChartData}
            timeChartData={timeChartData}
            onSwitchView={onSwitchView}
          />
          <TabItem
            key={1}
            store={store}
            tab={EnumDimension["month"]}
            mode={"month"}
            format={"YYYY-MM"}
            view={view}
            active={curPage === EnumDimension["month"]}
            overviewData={overviewData}
            alarmTypeData={alarmTypeData}
            numberChartData={numberChartData}
            timeChartData={timeChartData}
            onSwitchView={onSwitchView}
          />
          <TabItem
            key={2}
            store={store}
            tab={EnumDimension["year"]}
            mode={"year"}
            format={"YYYY"}
            view={view}
            active={curPage === EnumDimension["year"]}
            overviewData={overviewData}
            alarmTypeData={alarmTypeData}
            numberChartData={numberChartData}
            timeChartData={timeChartData}
            onSwitchView={onSwitchView}
          />
        </Tabs>
      </>
    );
  }
}

export default AlarmStatistics;
