import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import DatePickerSelect from "@/components/DatePickerSelect";
import LineChart from "@/components/Chart-line";
import Overview from "../overview";
import AlarmType from "../alarmType";
import { EnumView, ITabItemProps, IOverview, IAlarmType } from "../constance";

class TabItem extends React.Component<GProps> {
  render() {
    const { tab, mode, overviewData, alarmTypeData, numberChartData, active, store, format } = this.props;
    const { evaluationValue } = store;
    const data = alarmTypeData.filter((item: IAlarmType) => item.value);
    return (
      <div>
        <DatePickerSelect label="统计时间" mode={mode} format={format} onChange={evaluationValue} />
        <div className="alarm-statistics-section">
          <Overview>
            {overviewData.map((item: IOverview, i: number) => (
              <Overview.item key={i} {...item} />
            ))}
          </Overview>
          {active && (
            <div className="alarm-statistics-chart-wrap">
              <div className="alarm-statistics-chart-title">数量趋势</div>
              <LineChart key={tab} mark={`number-${tab}`} className="alarm-statistics-chart" active={active} data={numberChartData} />
            </div>
          )}
          <AlarmType>
            {data.map((item: IAlarmType, index: number) => (
              <AlarmType.item key={index} index={index + 1} {...item} />
            ))}
          </AlarmType>
        </div>
      </div>
    );
  }
}

export default TabItem;
