import React from 'react';
import { IOverview } from '../constance';
require('./style.scss');

interface IProps {
    children?: any;
}

const Overview = (props: IProps) => {
    const { children } = props;
    return (
        <div className="alarm-statistics-overview-wrap">
            <ul className="alarm-statistics-overview">{children}</ul>
        </div>
    );
};

const Item = React.memo((props: IOverview) => {
    const { label, value } = props;
    return (
        <li>
            <span className="alarm-statistics-overview-value">{value}</span>
            <span className="alarm-statistics-overview-title">
                <span className="alarm-statistics-overview-label">{label}</span>
            </span>
        </li>
    );
});

Overview.item = Item;

export default Overview;
