import React, { useState, useEffect, useMemo } from "react";
import { DatePicker, Icon } from "antd-mobile";
import dayjs from "dayjs";
require("./style.scss");

interface IProps {
  label: string;
  mode: string;
  onChange: Function;
  handle?: TReactNode;
  defaultDate?: Date;
  minDate?: Date;
  maxDate?: Date;
  alarmStatisticsStore: any;
}

interface IState {
  date: Date;
  curTime: any;
}

const DatePickerSelect = (props: any) => {
  const [date, setDate] = useState(undefined);
  useEffect(() => {
    if (props.defaultValue) {
      setDate(props.defaultValue);
    }
  }, [props.defaultValue]);
  const onChange = (data: Date) => {
    setDate(data);
    props.onChange(data);
  };
  return useMemo(
    () => (
      <div className="datePicker-select-wrap">
        <div className="datePicker-select">
          <div className="datePicker-select-label">{props.label}</div>
          <div className="datePicker-select-value">
            <DatePicker
              mode={props.mode}
              title="选择日期"
              maxDate={new Date(Date.now())}
              format={props.format || "YYYY-MM-DD"}
              value={date}
              onChange={onChange}
            >
              <div className="datePicker-select-content">
                <span>{dayjs(date).format(props.format || "YYYY-MM-DD")}</span>
                <Icon type="down" size="sm" />
              </div>
            </DatePicker>
          </div>
          <div className="datePicker-select-handle">{props.handle || null}</div>
        </div>
      </div>
    ),
    [props.mode, props.format, date, props.handle, props.label]
  );
};

export default DatePickerSelect;
