import React from 'react';
import { IAlarmType } from '../constance';

require('./style.scss');

interface IProps {
    children?: any;
}

const AlarmType = (props: IProps) => {
    const { children } = props;
    return (
        <div className="alarm-statistics-alarmType">
            <div className="alarm-statistics-alarmType-title">
                报警类型数量TOP10
            </div>
            {children && children.length > 0 ? (
                <ul className="alarm-statistics-alarmType-ul">{children}</ul>
            ) : (
                <div className="alarm-statistics-alarmType-empty">暂无数据</div>
            )}
        </div>
    );
};

const Item = React.memo((props: IAlarmType) => {
    const { label, value, rate, index } = props;
    return (
        <>
            <li>
                <span className="alarm-statistics-alarmType-item-index">
                    {index}
                </span>
                <div className="alarm-statistics-alarmType-item-label">
                    {label}
                </div>
                <div className="alarm-statistics-alarmType-item-main">
                    <span className="alarm-statistics-alarmType-item-value">
                        数量: <span>{value}</span>
                    </span>
                    <span className="alarm-statistics-alarmType-item-value">
                        占比: <span>{`${rate}`}</span>
                    </span>
                </div>
            </li>
        </>
    );
});

AlarmType.item = Item;

export default AlarmType;
